.header {
    background: -webkit-linear-gradient(
        top left,
        rgb(30, 30, 30),
        75%,
        rgb(160, 0, 0)
    );
    background: linear-gradient(
        to bottom right,
        rgb(30, 30, 30),
        75%,
        rgb(160, 0, 0)
    );
    height: 250px;
}
.headerBox {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    height: 250px;
    -webkit-justify-content: center;
            justify-content: center;
    margin: auto;
    width: 250px;
}

.headerH1 {
    color: white;
    font-size: 100px;
}

@media screen and (max-width: 700px) {
    .headerH1 {
        font-size: 75px;
    }
}

.form {
    margin: auto;
    text-align: center;
}

.formSearchBar {
    border: 1px solid black;
    border-radius: 3px;
    height: 35px;
    font-size: 17px;
    font-weight: 400;
    padding-left: 5px;
    width: 300px;
}

.searchButton {
    margin-top: 10px;
    background: -webkit-linear-gradient(top left, black, 75%, red);
    background: linear-gradient(to bottom right, black, 75%, red);
    border: 1px solid black;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    font-size: 18px;
    height: 35px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    width: 200px;
}

.searchButton:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.switch {
    margin: auto;
    width: 135px;
    padding: 10px;
}

.onoffswitch {
    position: relative;
    width: 90px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
.onoffswitch-checkbox {
    display: none;
}
.onoffswitch-label {
    display: block;
    overflow: hidden;
    cursor: pointer;
    border: 2px solid #827f7f;
    border-radius: 20px;
}
.onoffswitch-inner {
    display: block;
    width: 200%;
    margin-left: -100%;
    -webkit-transition: margin 0.3s ease-in 0s;
    transition: margin 0.3s ease-in 0s;
}
.onoffswitch-inner:before,
.onoffswitch-inner:after {
    display: block;
    float: left;
    width: 50%;
    height: 30px;
    padding: 0;
    line-height: 30px;
    font-size: 14px;
    color: white;
    font-family: Trebuchet, Arial, sans-serif;
    font-weight: bold;
    box-sizing: border-box;
}
.onoffswitch-inner:before {
    content: "Star";
    padding-left: 10px;
    background-color: rgb(30, 30, 30);
    color: #ffffff;
}
.onoffswitch-inner:after {
    content: "Poke";
    padding-right: 10px;
    background-color: rgb(200, 0, 0);
    color: #ffffff;
    text-align: right;
}
.onoffswitch-switch {
    display: block;
    width: 18px;
    margin: 6px;
    background: #ffffff;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 56px;
    border: 2px solid #827f7f;
    border-radius: 20px;
    -webkit-transition: all 0.3s ease-in 0s;
    transition: all 0.3s ease-in 0s;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
    margin-left: 0;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
    right: 0px;
}

